.delivery-closures-original {

    font-size: $font-size-sm;

    .delivery-headings {
        text-transform: uppercase;
        color: $cardinal-gray-600;
        letter-spacing: 1.5px;
        font-weight: bold;
    }

    .info {
        font-size: .9rem;
        margin-top: 16px; //center align with button regardless of column height
        margin-left: .33rem;
    }

    .holiday-date {
        line-height: 1;
    }

    .btn {
        width: 100%;

        @include media-breakpoint-up(md) {
            width: auto;
            margin-right: $gutters-desktop;
        }
    }
}

.delivery-closures {

    font-size: $font-size-sm;


    .calendar-info {

        background-color: $gray-100;

        .year-picker {
            display: flex;
            color: $gray-600;
            align-items: center;

            @include media-breakpoint-up(md) {
                justify-content: center;
            }
        }
    }

    .calendar-container {

        background-color: $gray-100;

        .calendars {

            position: relative;
            padding-right: 0;
            padding-left: 0;
            margin-right: auto;
            margin-left: auto;

            max-width: 370px;

            @include media-breakpoint-up(md) {
                padding-right: 15px;
                padding-left: 15px;
                max-width: 770px;
            }

            @include media-breakpoint-up(xl) {
                max-width: 1140px;
            }

            .scrollmenu {
                overflow: hidden;
                white-space: nowrap;
                padding: 0;

                h3 {
                    margin-bottom: 1.75rem;
                    padding-left: 5px;

                    @include media-breakpoint-up(md) {
                        padding-left: 0;
                    }
                }
            }

            .next-left {
                position: absolute;
                z-index: 99;
                top: 18px;
                right: 60px;

                @include media-breakpoint-up(md) {
                    right: unset;
                    left: 20px;
                }
            }

            .next-right {
                position: absolute;
                z-index: 99;
                top: 18px;
                right: 13px;

                @include media-breakpoint-up(md) {
                    right: 25px;
                }
            }
        }

        table {
            border-collapse: separate;
            border-spacing: 0;
            margin-left: 2px;
        }

        .btn-icon {
            font-size: 1.75rem;
            color: $cardinal-black;
        }

    }

    .confirmation-email {
        font-weight: bold;
        border-top: 1px solid $gray-200;
        border-bottom: 1px solid $gray-200;
        width: 100%;
    }

}

.modal-content {

    .select-more {

        .selected-store {
            padding-top: .75rem;
            padding-bottom: .75rem;
        }

        .btn {
            width: 265px;
        }
    }

    .multi-store {

        .modal-body {

            .stores {

                max-height: 400px;
                overflow-y: scroll;

                .store-location {
                    border-bottom: 1px solid $cardinal-med-gray;

                    .account-num {
                        font-size: $font-size-xs;
                        color: $cardinal-gray-600;
                    }
                }
            }

            .btn {
                width: 48%;
                margin: 0;
                padding-left: 0;
                padding-right: 0;
            }
        }

    }

    .confirmation {

        .confirmation-email {
            font-weight: bold;
            border-top: 1px solid $gray-200;
            border-bottom: 1px solid $gray-200;
            width: 100%;
        }

        .modal-footer {

            flex-direction: column;

            .btn-primary {
                width: 266px;
            }
        }
    }
}
