/*** Radio Button Styles ***/
.radio {
  display: block;
  position: relative;
  padding-left: 1.6rem;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.radio input {
  position: absolute;
  opacity: 0;
  left: 0;
  cursor: pointer;
}

/*Create a custom radio button*/
.filled-in {
  position: absolute;
  top: 0;
  left: 0;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  border: 1px solid black;
}

/*Create the indicator (the dot/circle - hidden when not checked)*/
.filled-in:after {
  content: "";
  position: absolute;
  display: none;
}

/*Show the indicator (dot/circle) when checked*/
.radio input:checked ~ .filled-in:after {
  display: block;
}

/*Style the indicator (dot/circle)*/
.radio .filled-in:after {
  top: 3px;
  left: 3px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: black;
}

