input[type="checkbox"] {
    height: 18px;
    width: 18px;
}

.form-check-label {
    padding-top: .25rem;
    padding-left: .5rem;
}

.forms {

    .form-group {
        position: relative;
        margin-bottom: 1.5rem;

        &.floating-label {

            input[type=email],
            input[type=text] {
                padding: 1.75rem 0 0.75rem .65rem;
            }

            .form-control-placeholder {
                position: absolute;
                top: .25rem;
                padding: 0.4375rem 0 0 0.8125rem;
                transition: all 200ms;
            }

            .form-control:focus + .form-control-placeholder,
            .form-control:valid + .form-control-placeholder {
                font-size: 75%;
                transform: translate3d(0, -15%, 0);
            }

        }

    }

}

/*** Radio Button Styles ***/
.radio {
    display: block;
    position: relative;
    padding-left: 1.6rem;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

}

/* Hide the browser's default radio button */
.radio input {
    position: absolute;
    opacity: 0;
    left: 0;
    cursor: pointer;
}

/*Create a custom radio button*/
.filled-in {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    border: 1px solid black;
}

/*Create the indicator (the dot/circle - hidden when not checked)*/
.filled-in:after {
    content: "";
    position: absolute;
    display: none;
}

/*Show the indicator (dot/circle) when checked*/
.radio input:checked ~ .filled-in:after {
    display: block;
}

/*Style the indicator (dot/circle)*/
.radio .filled-in:after {
    top: 3px;
    left: 3px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: black;
}
