$header-nav-color: #27251f;

//(This is V1 code. Header V2 code is below)
.header {

    //Shared styles
    .return-oe {
        right: 0;
        height: 100%;
        color: $cardinal-white;
        background-color: $cardinal-red;
        font-weight: $font-weight-semibold;
        line-height: 1.125;
        font-size: $font-size-xs;
        display: flex;
        justify-self: center;
        align-items: center;

        svg {
            font-size: 1.125rem;
        }
    }

    .header-update {
        position: absolute;
        width: 100%;
        bottom: 0px;
        background-color: rgba(40, 38, 32, 0.5); //$cardinal-black with transparency
        height: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: $font-size-sm;

        a {
            color: $white;
        }

    }


    //mobile specific styles
    .header-small {

        display: block;

        @include media-breakpoint-up(lg) {
            display: none;
        }

        svg {
            font-size: 2rem;
        }

        .navbar {

            padding: 0;

            .navbar-brand {
                img {
                    width: 80px;
                }
            }

            .navbar-toggler {
                padding: .25rem .63rem .25rem 0;
                color: $cardinal-black;
                width: 75px;
            }

            .navbar-nav {

                .nav-item {
                    border-top: 1px solid $gray-100;

                    &:hover {
                        background-color: $gray-200;
                    }

                    .nav-link {
                        padding: 1rem $gutters-mobile;
                    }
                }
            }

            .alert-spacer {
                margin-left: 0;

                @media (min-width: 350px) {
                    margin-left: 1rem;
                }

                @include media-breakpoint-up(sm) {
                    margin-left: 3rem;
                }
            }

            .nav-right {

                display: flex;
                align-items: center;

                .header-link {
                    margin-right: 1rem;

                    @include media-breakpoint-up(sm) {
                        margin-right: 3rem;
                    }
                }

                .return-oe {
                    padding: .5rem 1rem .5rem 1.25rem;

                    svg {
                        font-size: 1.125rem;
                    }
                }
            }
        }

        .account-bar {
            background-color: $cardinal-black;
            font-size: $font-size-sm;

            .dropdown-toggle {
                color: $cardinal-white;
                display: flex;
                align-items: center;
                padding: .45rem 0 .33rem $gutters-mobile;
                cursor: pointer;
            }

            .dropdown-menu {
                width: 100%;
                transform: translate3d(0px, 42px, 0px) !important; //overriding default bootstrap transform
                border: 1px solid $gray-100;

                .dropdown-item {
                    height: 2.5rem;
                    display: flex;
                    align-items: center;
                    padding: 0 1rem 0 3.4rem;
                }

                .dropdown-icon {
                    padding-left: 1rem;
                }
            }
        }

        .header-sm-main {
            color: $white;
            background-color: $primary;
            margin: auto;
            text-align: center;
            padding: 2rem 0;
            position: relative;

            .title {
                font-size: $h1-font-size;
                font-weight: $font-weight-light;
                line-height: 1.1;
            }

            .header-update {
                padding-left: $gutters-mobile;
                padding-right: $gutters-mobile;
                font-size: $font-size-xs;

                svg {
                    font-size: 1.5rem;
                }
            }

        }
    }



    //desktop specific styles
    .header-large {

        display: none;
        @include media-breakpoint-up(lg) {
            display: block;
        }

        .return-oe {
            width: $gutters-desktop;
            padding: 0 1rem;
        }

        .header-top {
            padding-left: $gutters-desktop;
            display: flex;
            justify-content: space-between;

            .left {
                img {
                    width: 150px;
                    margin-left: -28px; //set wings off to left in gutter
                    padding-top: 1rem;
                    padding-bottom: 1rem;
                }
            }

            .right {
                display: flex;
                align-items: center;

                .header-link {
                    color: $black;
                    display: flex;
                    align-items: center;
                    font-size: $font-size-sm;
                    border-left: 1px solid transparent;
                    border-right: 1px solid transparent;

                    &:hover, &:focus, &:active {
                        border-left: 1px solid $gray-200;
                        border-right: 1px solid $gray-200;
                        box-shadow: 0 3px 6px 0 rgba(40, 38, 32, 0.16);
                    }

                    &:first-child {
                        padding-left: 1.5rem;
                        padding-right: 1.5rem;
                    }

                    svg {
                        font-size: 2rem;
                    }
                }

                .dropdown {
                    height: 100%;

                    .dropdown-toggle {
                        padding: 0 1.65rem;
                        text-decoration: none;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        min-width: 190px;
                    }

                    .dropdown-menu {
                        max-height: 400px;
                        overflow-y: auto;
                        width: 310px;
                        margin-top: 0;
                        border: 1px solid $gray-100;

                        .dropdown-item {
                            padding: 0 1rem 0 4.25rem;
                            height: 3rem;
                            display: flex;
                            align-items: center;


                            svg {
                                font-size: 2rem;
                                margin-right: .25rem;
                            }
                        }

                        .dropdown-icon {
                            padding-left: 1.5rem;
                            padding-right: 3rem;
                        }
                    }

                    .label {
                        max-width: 180px;
                    }
                }

            }

        }

        .header-nav {
            padding: .75rem $gutters-desktop;

            background-color: $header-nav-color;

            display: flex;
            flex-direction: row;
            justify-content: space-between;

            a {
                color: $white;
            }
        }

        .header-lg-main {
            position: relative;
            color: $white;

            //considerations for accessibility
            background-color: $cardinal-red;
            min-height: 165px;

            img{
                width: 100%;
            }

            .header-main-content {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;

                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

                .title {
                    font-size: $font-size-title;
                    font-weight: $font-weight-light;
                    width: 75%;
                    text-align: center;
                    line-height: 1.1;
                }

            }

            .header-update {
                padding-left: $gutters-desktop;
                padding-right: $gutters-desktop;

                svg {
                    font-size: 1.5rem;
                }
            }
        }
    }
}

