// Set Bootstrap SASS theme variables here


@import url('https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,500i,700,700i,900,900i&display=swap');

$font-family-sans-serif: 'Roboto', sans-serif;

//Typography
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-semibold: 500;
$font-weight-bold: 700;
$font-weight-black: 900;


//Cardinal Color Palette
$cardinal-red: #e41f35;
$cardinal-red-text: #d51a30;
$cardinal-red-error: #D51A30;
$cardinal-red-active: #6d151f;
$cardinal-red-active-light: #f0e7e8;
$cardinal-white: #fff;
$cardinal-black: #27251F;
$cardinal-blue: #bbdde6;
$cardinal-teal: #346170;
$cardinal-green: #06890e;
$cardinal-yellow: #fcc954;
$cardinal-light-gray: #efefef;
$cardinal-med-gray: #e0e0e0;
$cardinal-dark-gray: #948a85;

$cardinal-gray-100: #f1f1f1;
$cardinal-gray-200: #efefef;
$cardinal-gray-600: #666666;

// Bootstrap Color Palette
$white: $cardinal-white;
$gray-100: $cardinal-gray-100;
$gray-200: $cardinal-gray-200;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: $cardinal-gray-600;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;

$black: $cardinal-black;
$blue: $cardinal-blue;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$red: $cardinal-red;
$orange: #fd8a11;
$yellow: $cardinal-yellow;
$green: $cardinal-green;
$teal: $cardinal-teal;
$cyan: #04a1a8;


// Semantic Color Settings
$primary: $red;
$secondary: $black;
$success: $green;
$info: $teal;
$warning: $yellow;
$danger: $cardinal-red-error;
$body-bg: $white;
$body-color: $black;

@import './vars';
@import './common';
