.notification {
    font-size: $font-size-sm;
    //width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1.5rem;

    margin-top: 2rem;
    margin-bottom: 2rem;
    margin-left: $gutters-mobile;
    margin-right: $gutters-mobile;

    @include media-breakpoint-up(md) {
        margin-left: $gutters-desktop;
        margin-right: $gutters-desktop;
    }

    svg {
        font-size: 1rem;
    }

    &.warning {
        border-radius: 6px;
        border: 1px solid $cardinal-red;
        border-left: 9px solid $cardinal-red;

        svg, a {
            color: $cardinal-red;
        }

        .close {
            opacity: 1;

            svg {
                color: $cardinal-black;
                display: block;
            }
        }

    }

    &.info {
        border-radius: 6px;
        border: 1px solid $cardinal-gray-600;
        border-left: 9px solid $cardinal-gray-600;

        svg {
            color: $cardinal-gray-600;
        }

        a {
            color: $cardinal-red;
        }

        .close {
            opacity: 1;

            svg {
                color: $cardinal-black;
                display: block;
            }
        }

    }
}
