.modal {

    font-size: $font-size-sm;

    .close {

        opacity: 1;

        svg {
            font-size: 1.25rem;
            color: $cardinal-black;
        }
    }

    .header-icon {
        font-size: 2rem;
    }

    .title {
        font-size: 28px;
        font-weight: $font-weight-light;
        line-height: 1.25;
    }

    .add-email {
        border: none;
        background-color: transparent;
        padding-left: 0;

        svg {
            font-size: 1.25rem;
        }
    }

    .form-control {
        font-size: $font-size-sm;
    }

    .modal-body {
        padding: 0 2rem;
    }

    .modal-footer {
        padding: 1rem 2rem 2rem;
    }
}
