.footer {
    background-color: $cardinal-black;
    color: $cardinal-white;
    text-align: center;

    .footer-op-link {
        background-color: $gray-600;
        padding: .75rem 0;

        a {
            color: $cardinal-white;
            text-transform: uppercase;
            text-decoration: none;
            font-size: $font-size-sm;
            letter-spacing: 1.34px;
            font-weight: $font-weight-semibold;

            .icon {
                font-size: 1rem;
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .footer-main {
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding-bottom: .5rem;

        @include media-breakpoint-up(md) {
            flex-direction: row;
        }

        a {
            color: $cardinal-white;
            text-decoration: none;
            margin: 0 1rem .5rem;
            font-size: $font-size-sm;

            @include media-breakpoint-up(md) {
                width: 220px;
            }
        }

        svg {
            font-size: 1.25rem;
            margin: 0 auto 1rem;
            order: -1;

            @include media-breakpoint-up(md) {
                order: 0;
                margin: 0 1rem .5rem
            }
        }
    }

    .footer-links {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        a {
            font-size: $font-size-xs;
            color: $cardinal-white;
            padding: 0 1rem .5rem;

            @include media-breakpoint-up(md) {
                width: 125px;
            }
        }
    }

    .footer-copyright {
        font-size: 10px;
        padding-top: .5rem
    }

}
