// Bootstrap configured base styles
@import "bootstrap/scss/bootstrap";
// Component styles
@import './components/buttons';
@import './components/calendar-footer';
@import './components/contrast';
@import './components/delivery-closures';
@import './components/footer';
@import './components/forms';
@import './components/header';
@import './components/icon';
@import './components/modals';
@import './components/month';
@import './components/navbar';
@import './components/notifications';
@import './components/preference-center';
@import './components/swatch';


//Helper Classes

.truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.add-page-gutters {
    padding-left: $gutters-mobile;
    padding-right: $gutters-mobile;

    @include media-breakpoint-up(lg) {
        padding-left: $gutters-desktop;
        padding-right: $gutters-desktop;
    }
}

.add-left-page-gutter {
    padding-left: $gutters-mobile;

    @include media-breakpoint-up(lg) {
        padding-left: $gutters-desktop;
    }
}

.add-right-page-gutter {
    padding-right: $gutters-mobile;

    @include media-breakpoint-up(lg) {
        padding-right: $gutters-desktop;
    }
}

.font-size-xs {
    font-size: $font-size-xs;
}

body, html {
    height: 100%;
}
