.btn,
.btn-date,
.btn-year,
.btn-icon,
.btn-date-with-month {
    &:focus,
    &:active {
        outline: dotted $cardinal-black 1px;
        box-shadow: none !important;
        outline-offset: 2px;
    }
}

.btn-primary {
    &:hover {
        background-color: $cardinal-black;
        border-color: $cardinal-black;
        box-shadow: none;
    }
}
.btn-primary:not(:disabled):not(.disabled) {
    &:active {
        background-color: $cardinal-red-active;
        border-color: $cardinal-red-active;
        box-shadow: none;
    }
}

.btn-primary {
    &:disabled {
        color: $cardinal-gray-600;
        background-color: $cardinal-med-gray;
        border-color: $cardinal-med-gray;
        box-shadow: none;
        opacity: 1;
        &:hover {
            cursor: not-allowed;
        }
    }
}

.btn-outline-primary {
    color: $cardinal-black;
    background-color: transparent;
    border: 1px solid $cardinal-black;
    &:hover {
        color: $cardinal-black;
        background-color: $cardinal-gray-100;
        border: 1px solid $cardinal-black;
    }

}

.btn-outline-primary:not(:disabled):not(.disabled) {
    &:active {
        color: $cardinal-red-active;
        background-color: $cardinal-red-active-light;
        border-color: $cardinal-red-active;
        box-shadow: none;
    }
}

.btn-outline-primary {
    &:disabled {
        color: $cardinal-gray-600;
        background-color: $white;
        border: 1px solid $cardinal-med-gray;
        box-shadow: none;
        opacity: 1;
        &:hover {
            cursor: not-allowed;
        }
    }
}

.btn-gray {
    color: $cardinal-white;
    background-color: $cardinal-gray-600;

    &:hover {
        color: $cardinal-white;
        background-color: $cardinal-black;
    }
}

.btn-icon {
    border: none;
    background-color: transparent;
    font-size: 1.25rem;
    display: inline-flex;
}

.btn-year {
    border: none;
    background-color: transparent;
    color: $gray-600;
    font-size: 1rem;
    font-weight: $font-weight-bold;
    display: inline-flex;
    padding: 0 .5rem;

    &.active, &:active {
        box-shadow: none !important;
        color: $cardinal-red-text;
    }
}

.btn-date-with-month {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 54px;
    border: none;
    border-radius: 6px;
    background-color: $red;
    color: $cardinal-white;
    font-size: $font-size-xs;
    line-height: 1.25;
    padding: 0;
    margin: 0 .5rem .5rem 0;
    cursor: pointer;

    input {
        position: absolute;
        visibility: hidden;
    }

    .day {
        text-transform: uppercase;
        font-size: 8px;
        padding-top: .125rem;
        font-weight: $font-weight-semibold;
    }

    .logo {
        width: 30px;
        height: 12px;
    }

    svg {
        font-size: .75rem;
    }

    &:focus {
        outline: 5px auto -webit-focus-ring-color;
    }

    &.unselected {
        background-color: $cardinal-white;
        color: $cardinal-black;
        border: 1px solid $cardinal-black;
        margin: 0 0 .5rem .5rem;

        &:hover {
            background-color: $cardinal-black;
            color: $cardinal-white;
            transition: 0.3s;

            .icon {
                background-color: $cardinal-white;
            }
        }
    }

    &.selected {
        background-color: $cardinal-black;
        color: $cardinal-white;
        margin: 0 0 .5rem .5rem;
    }

    &.passed-selected {
        background-color: $cardinal-gray-600;
        margin: 0 0 .5rem .5rem;
        cursor: default;
    }

    &.passed-unselected {
        color: $cardinal-gray-600;
        background-color: $cardinal-white;
        border: 1px solid $cardinal-gray-600;
        margin: 0 0 .5rem .5rem;
        cursor: default;

        .icon {
            color: $cardinal-white;
        }
    }

}

.btn-date {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 54px;
    border-radius: 6px;
    font-size: $font-size-xs;
    line-height: 1.25;
    padding: 0;
    margin: 0 2px .5rem 0;
    border: none;
    background-color: $red;
    color: $cardinal-white;
    cursor: default;

    input {
        position: absolute;
        visibility: hidden;
    }

    .logo {
        width: 30px;
        height: 12px;
    }

    svg {
        font-size: .75rem;
    }

    .day {
        text-transform: uppercase;
        font-size: 8px;
        padding-top: .125rem;
        font-weight: $font-weight-semibold;
        line-height: 1.67;
    }

    .number {
        font-size: $font-size-base;
        line-height: 1;
    }

    &:focus {
        outline: 5px auto -webit-focus-ring-color;
    }

    &.today {
        position: relative;

        &:after {
            content: "";
            position: absolute;
            font-size: 24px;
            color: $cardinal-red;
            top: -17px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &.unselected {
        background-color: $cardinal-white;
        color: $cardinal-black;
        border: 1px solid $cardinal-black;
        cursor: pointer;

        .icon {
            color: $cardinal-white;
        }

        &:hover {
            @supports (display: grid) {
                @include media-breakpoint-up(lg) {
                    background-color: $cardinal-black;
                    color: $cardinal-white;
                    transition: 0.3s;
                }
            }
        }
    }

    &.selected {
        background-color: $cardinal-black;
        color: $cardinal-white;
        cursor: pointer;
    }

    &.passed {
        background-color: $cardinal-gray-600;
        color: $cardinal-white;
        cursor: default;
    }

    &.passed-selected {
        background-color: $cardinal-gray-600;
        color: $cardinal-white;
        cursor: default;
    }

    &.passed-unselected {
        color: $cardinal-gray-600;
        background-color: $gray-100;
        border: 1px solid $cardinal-gray-600;
        cursor: default;

        .icon {
            color: $gray-100;
        }
    }

}

//styles specifically for angular component
td {
    &:last-child {
        .btn-date {
            margin: 0 0 .5rem 0;
        }
    }
}
