.calendar-footer {
    position: relative;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: $cardinal-blue;
    overflow: hidden;
    transition: all ease .5s;

    &.stuck {
       position: sticky;
       z-index: 100;
    }

    img {
        display: none;
        @include media-breakpoint-up(md) {
            display: flex;
            position: absolute;
            left: -36px;
            top: -10px;
            height: 126%;
        }
    }

    .btn {
        margin: .5rem $gutters-mobile;
        width: 100%;

        @include media-breakpoint-up(md) {
            width: 240px;
            margin: 0 .5rem;

            &:first-child {
                margin-right: $gutters-mobile;
            }
        }

        @include media-breakpoint-up(lg) {
            &:first-child {
                margin-right: $gutters-desktop;
            }
        }
    }

}
