
// Color Usage
$link-color: $secondary;
$component-active-color: $body-color;

// Options
$enable-shadows: true;
$enable-responsive-font-sizes: true;
$border-width: 2px;

// Base Font Size
$font-size-base: 1rem; // Assumes the browser default, typically `16px`
$font-size-lg: $font-size-base * 1.25;
$font-size-sm: $font-size-base * .875;
$font-size-xs: $font-size-base * .75;
$font-size-title: $font-size-base * 3;


// Headings
$h1-font-size: $font-size-base * 2; //32px
$h2-font-size: $font-size-base * 1.75; //28px
$h3-font-size: $font-size-base * 1.375; //22px
$h4-font-size: $font-size-base * 0.9375; //15px
$h5-font-size: $font-size-base * 0.875; //14px
$h6-font-size: $font-size-base * 0.75;  //12px

$line-height-base: 1.45;
$headings-line-height: 1.75;

//Gutters
$gutters-desktop: 85px;
$gutters-mobile: 16px;


//Dropdowns
$dropdown-divider-margin-y: 0;
$dropdown-divider-bg: $gray-100;
$dropdown-padding-y: 0;
$dropdown-link-active-color: $cardinal-white;
$dropdown-link-active-bg: $cardinal-black;
$dropdown-border-radius: 0;
$dropdown-border-width: 1px;


//Spacers
$spacer: 1rem;
$spacers: ();
$spacers: map-merge(
        (
            0: 0,
            1: ($spacer * .25),
            2: ($spacer * .5),
            3: $spacer,
            4: ($spacer * 1.5),
            5: ($spacer * 2),
            6: ($spacer * 3)
        ),
        $spacers
);


//Buttons
$btn-border-radius: 4px;
$btn-padding-x: 3.5rem;
$btn-padding-y: .75rem;
$btn-font-size: $font-size-sm;

//Checkboxes
$custom-checkbox-indicator-border-radius: 4px;


//Modals
$modal-content-border-radius: 6px;
$modal-header-border-color: transparent;
$modal-footer-border-color: transparent;


//Borders
$border-color: $cardinal-med-gray;
$border-width: 1px;
